import React from 'react';

interface PricingCardProps {
    title: string;
    price: string;
    features?: string[]; // made optional to avoid runtime crashes when not provided
    buttonText: string;
}

const PricingCard: React.FC<PricingCardProps> = ({ title, price, features = [], buttonText }) => {
    return (
        <div className="pricing-card">
            <div>
                <h2 className="pricing-title">{title}</h2>
                <p className="pricing-price">{price}</p>
                <ul className="pricing-features features-list">
                    {(features || []).map((feature, index) => (
                        <li key={`${feature}-${index}`} className="mb-2">
                            {feature}
                        </li>
                    ))}
                    {(!features || features.length === 0) && (
                        <li className="text-gray-500">Nenhuma característica listada.</li>
                    )}
                </ul>
            </div>
            <div className="price-cta">
                <button className="btn-primary">{buttonText}</button>
            </div>
        </div>
    );
};

export default PricingCard;